import { useState } from "react";

export const useArray = <T,>(persons: T[]) => {
  const [value, setValue] = useState<T[]>(persons);
  const add = (person: T) => {
    setValue([...value, person]);
  };
  const removeIndex = (index: number) => {
    value.splice(index, 1);
    setValue([...value]);
  };

  return { value, add, removeIndex };
};
