import React, { useEffect, useState } from "react";
import styled from "@emotion/styled";
import { Button, Layout, Menu } from "antd";
import { Outlet, useNavigate } from "react-router-dom";
import {
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  UploadOutlined,
  UserOutlined,
  VideoCameraOutlined,
} from "@ant-design/icons";

const { Header, Sider, Content } = Layout;

interface MenuItemType {
  key: string;
  icon: React.ReactNode;
  label: string;
}

interface MenuInfo {
  key: string;
  keyPath: string[];
  /** @deprecated This will not support in future. You should avoid to use this */
  item: React.ReactInstance;
  domEvent: React.MouseEvent<HTMLElement> | React.KeyboardEvent<HTMLElement>;
}

const BaseLayout: React.FC = () => {
  const [data, setData] = useState<MenuItemType[]>();
  const [collapsed, setCollapsed] = useState<boolean>();
  const navigate = useNavigate();

  useEffect(() => {
    setTimeout(() => {
      setData([
        {
          key: "1",
          icon: <UserOutlined />,
          label: "nav 1",
        },
        {
          key: "2",
          icon: <VideoCameraOutlined />,
          label: "nav 2",
        },
        {
          key: "3",
          icon: <UploadOutlined />,
          label: "nav 3",
        },
      ]);
    }, 2000);
  }, []);

  const clickMenuItem = (info: MenuInfo) => {
    console.log(info);
    switch (info.key) {
      case "1":
        navigate("/user");
        break;
      case "2":
        navigate("/some-demo");
        break;
      default:
        navigate("");
        break;
    }
  };

  return (
    <Layout>
      <Header>Header</Header>
      <Layout hasSider>
        <HiSider
          breakpoint="xs"
          onBreakpoint={(boo) => {
            console.log(boo);
          }}
          collapsible
          collapsed={collapsed} //Sider/MenuMenu
          trigger={null}
        >
          <Menu mode="inline" items={data} onClick={clickMenuItem}></Menu>

          {/* inlineCollapsedMenuMenuSlideSlideApiMenu/ */}
          {/* <Menu mode="inline" items={data} inlineCollapsed={collapsed}></Menu> */}

          <HiButton onClick={() => setCollapsed(!collapsed)}>
            {collapsed ? <MenuFoldOutlined /> : <MenuUnfoldOutlined />}
          </HiButton>
        </HiSider>
        <HiContent>
          <Outlet />
        </HiContent>
      </Layout>
    </Layout>
  );
};

export default BaseLayout;

const HiSider = styled(Sider)`
  background-color: skyblue !important;
  height: calc(100vh - 64px);
  ul {
    border: none !important;
    background-color: skyblue !important;
  }
`;

const HiContent = styled(Content)`
  background-color: white !important;
  height: calc(100vh - 64px);
  padding: 8px;
`;

const HiButton = styled(Button)`
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  color: #ff0000;
`;
