import React, { FC } from "react";
import { Rate } from "antd";

interface HiRateProps extends React.ComponentProps<typeof Rate> {
  checked: boolean;
  changeChecked?: (checked: boolean) => void;
}

const HiRate: FC<HiRateProps> = ({
  checked,
  changeChecked,
  ...rest
}: HiRateProps) => {
  return (
    <Rate
      count={1}
      value={checked ? 1 : 0}
      onChange={(num) => changeChecked?.(!!num)}
      {...rest}
    />
  );
};

export default HiRate;
